import * as React from "react";

import Layout from "../components/layout";
import IRPetrov from "../images/persons/irpetrov.jpg";
import NSysoev from "../images/persons/nsysoev.jpg";
import AMinnegaraev from "../images/persons/aminnegaraev.jpg";
import ESmolyar from "../images/persons/esmolyar.jpg";
import NFesko from "../images/persons/nfesko.jpg";
import EOya from "../images/persons/eoya.jpg";
import EmployeeCard from "../components/EmployeeCard";

const TeamPage = () => (
  <Layout title="Команда Фонда | ФРИИС">
    <h1 className="page-header">Команда</h1>
    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
      <EmployeeCard image={IRPetrov} name={"Иван Петров"} position={"Генеральный директор"} />
      <EmployeeCard image={NSysoev} name={"Никита Сысоев"} position={"Финансовый директор"} />
      <EmployeeCard image={AMinnegaraev} name={"Айдан Миннегараев"} position={"Операционный директор"} />
      <EmployeeCard image={ESmolyar} name={"Есения Смоляр"} position={"Руководитель направления по работе с партнёрами"} />
      <EmployeeCard image={NFesko} name={"Нелли Фесько"} position={"Руководитель направления по организации мероприятий"} />
      <EmployeeCard image={EOya} name={"Евгения Оя"} position={"Делопроизводитель"} />
    </div>
  </Layout>
)

export default TeamPage
