import * as React from "react";


const EmployeeCard = ({ image, name, position }) => (
  <div className="flex flex-col md:flex-row items-center justify-center md:justify-start gap-6 border-brand border-2 p-4 rounded-2xl">
    {image && <img className="aspect-square rounded-full w-32" src={image} alt={name} />}
    <div className="employee-info">
      <h4 className="text-2xl text-center md:text-left text-brand font-bold mb-2">{name}</h4>
      {position && <p className="text-sm text-center md:text-left text-gray-500">{position}</p>}
    </div>
  </div>
);

export default EmployeeCard;
